import React from 'react'
import {Grid} from '@material-ui/core'
import { Typography } from '@material-ui/core';
import data from '../../assets/data/data';
import CustomTimeline, {CustomTimelineSeparator} from '../../components/Timeline/CustomTimeline';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineItem from '@material-ui/lab/TimelineItem';
import SchoolRoundedIcon from '@mui/icons-material/SchoolRounded';
import CustomCards from '../../components/Addons/CustomCards';
// import {Map, GoogleApiWrapper} from '@googlemaps/react-wrapper';
import Map from '../../components/Addons/Map';

import './Resume.css';


function Resume() {
    let number =200;

    function IncreaseNumber(){
        number = number +1;
    }

    return (
        <>
        <Grid container className="section pb_45">
            <Grid item className='title_section mb_30'>
                <span></span>
                <Typography className="title_section_text" variant='h6'>About Me</Typography>
            </Grid>
            <Grid item xs={12}>
                <Typography variant='body2' className='text_section_aboutme'>{data.about_me}</Typography>    
            </Grid>
        </Grid>
        <Grid container className="section pb_45">
            <Grid item className='title_section mb_30'>
                <span></span>
                <Typography className="title_section_text" variant='h6'>Resume</Typography>
            </Grid>
            <Grid container>
                    {/* <Grid item sm={12} md={6}>

                    </Grid> */}
                    <Grid item sm={12} md={6}>
                        <CustomTimeline className='custom_education' title='Education' icon={<SchoolRoundedIcon/>}>
                            {data.education.map((educations) => (
                                <TimelineItem>
                                    <CustomTimelineSeparator/>
                                    <TimelineContent>
                                        <div className='items'>
                                        <Typography className="title1">{educations.school}</Typography>
                                        <Typography className="date">{educations.date}</Typography>
                                        <Typography className="text">{educations.text}</Typography>
                                        </div>
                                    </TimelineContent>

                                </TimelineItem> 
                            ))}
                        </CustomTimeline>
                        
                    </Grid>
            </Grid>

        </Grid>
        <Grid container className="section pb_45">
            <Grid item className='title_section mb_30'>
                <span></span>
                <Typography className="title_section_text" variant='h6'>Skills</Typography>
            </Grid>
            <Grid container className='skills'>
                {/* <CustomCards/> */}

                {data.skills.map((skill,value) => (
                    <Grid item>
                        <CustomCards title={skill.title} text={skill.text} number={value +180}/>
                    </Grid>
                    
                ))}
                    
            </Grid>
        </Grid>
        <Grid container className="section">
            <Grid item className='title_section mb_30'>
                <span></span>
                <Typography className="title_section_text hideme" variant='h6'>Where to find me</Typography>
            </Grid>
            <Grid item className='google_map'>
                <Map/>
            </Grid>
            

        </Grid>
        </>
        
    )
}

export default Resume

