import React, {useRef} from 'react'
import {Grid} from '@material-ui/core'
import { Typography } from '@material-ui/core';
import data from '../../assets/data/data';
// import images from '../../assets/images/gif.gif'

import './Portfolio.css';
import logo from '../../assets/images/Portfolio/matriek.pdf';
import doc from '../../assets/data/portfolio'


import emailjs from '@emailjs/browser';



function Portfolio() {


    const form = useRef();
  
    const sendEmail = (e) => {
      e.preventDefault();
  
      emailjs.sendForm(data.emailkeyjs.SERVICE_ID, data.emailkeyjs.TEMPLATE_ID, form.current, data.emailkeyjs.USERID)
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
    };

    return (
        <div>
            <Grid container className="section pb_45">
                <Grid item className='title_section mb_30'>
                    <span></span>
                    <Typography className="title_section_text" variant='h6'>Portfolio</Typography>
                </Grid>        
            </Grid>
            {/* <Grid container className='portfolio_items'> */}
                {/* <CustomCards/> */}

                {/* {data.portfolio.map((skill, value) => (
                    <div className='portfolio_Item'>
                    <Grid item>
                        <embed className='portfolio_images' src={doc[value]} alt=""/>
                        <p className='img_title'>{skill.title}</p>
                    </Grid>
                    </div>
                    
                ))} */}
                    
            {/* </Grid> */}

            <Grid container className="section pb_45">
        <Grid item className='title_section mb_30'>
            <span></span>
            <Typography className="title_section_text" variant='h6'>Contact Me</Typography>
        </Grid>
        <Grid container>
            <Grid item className='input_contents'>
                <form ref={form} onSubmit={sendEmail}>
                    <Typography className='inputs'>
                        Name: <div className='inputs'><input type="text" name="firstname" className='input_box'/></div>
                    </Typography>
                    <Typography className='inputs'>
                        Last name: <div className='inputs'><input type="text" name="lastname" className='input_box'/></div>
                    </Typography>
                    <Typography className='inputs'>
                        Email: <div className='inputs'><input type="email" name="email" className='input_box'/></div>
                    </Typography>
                    <Typography className='inputs'>
                        Phone Number: <div className='inputs'><input type="tel" name="phone" className='input_box'/></div>
                    </Typography>
                    <Typography className='inputs'>
                        Message: <div className='inputs'><textarea name="message" className='message_box'></textarea></div>
                    </Typography>
                    <div className='button_div'>
                        <input type="submit" value="Contact Me" className='send_button'/>
                    </div>
                </form>
            </Grid>
        </Grid>        
    </Grid>
            
        </div>
    )
}


export default Portfolio
