import React, {useState}from 'react'
import {Button, Form, FormControl, Nav, Navbar, NavDropdown, Container} from 'react-bootstrap';
import {Link, NavLink, withRouter} from 'react-router-dom';
import data from '../../assets/data/data';
import {HomeRounded, SchoolRounded, WorkRounded} from '@mui/icons-material'
import CustomButton from '../Addons/CustomButton';
import MessageIcon from '@mui/icons-material/Message';
import logo from '../../assets/images/gif.gif'

import 'bootstrap/dist/css/bootstrap.min.css';
import './Header.css';

function Header(props) {
    const [buttonpopup, setButtonpopup] = useState(false)

    const pathName = props?.location?.pathname;
    return (
        <Navbar expand="lg" sticky='top' className='header'>
            <Nav.Link as={NavLink} to="/" className='nav_link'>
                <Navbar.Brand className='header_home'>
                    <HomeRounded/>
                </Navbar.Brand>
            </Nav.Link>
            <Navbar.Toggle/>

            <Navbar.Collapse>
                <Nav className='nav_left'>
                    <Nav.Link as= {NavLink} to="/" className={pathName == '/' ? 'header_link_active' : 'header_link'}>
                        Resume
                    </Nav.Link>
                    <Nav.Link as= {NavLink} to="/portfolio" className={pathName == '/portfolio' ? 'header_link_active' : 'header_link'}>
                        Portfolio
                    </Nav.Link>
                    {/* <Nav.Link as= {NavLink} to="/" className={pathName == '/' ? 'header_link_active' : 'header_link'}>
                        Contact Me
                    </Nav.Link> */}
                </Nav>
                <div className='header_right'>
                    {Object.keys(data.social_media).map(key => (
                        <a href={data.social_media[key].link} target='_blank'>
                            {data.social_media[key].icon}
                        </a>
                    ))}
                    <img className='gif_coding' src={logo} alt="loading..." />
                </div>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default withRouter(Header);
