import { Typography } from '@material-ui/core'
import React from 'react'
import './Profile.css';
import CustomTimeline, {CustomTimelineSeparator} from '../Timeline/CustomTimeline';
import data from '../../assets/data/data';
import TravelExploreIcon from '@mui/icons-material/TravelExplore';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineItem from '@material-ui/lab/TimelineItem';
import Button from '../Addons/CustomButton';
import selfimage from '../../assets/images/SelfImage.jpeg'

const openInNewTab = (url) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
    if (newWindow) newWindow.opener = null
  }

const CustomChildTimeline = ({title, text, link}) => (
    <TimelineItem>
        <CustomTimelineSeparator/>
        <TimelineContent>
            {link ? (<Typography className='header'>
               <span className='span_text'>{title}: </span>{" "}
               <a href={link} target='_blank'>{text}
               </a> 
            </Typography>
            ) : (
                <Typography className='header'>
                    <span className='span_text'>{title}: </span>{text}
                </Typography>
            )}
        </TimelineContent>
    </TimelineItem>
);

function Profile() {
    return (
        <div className='profile container_shadow'>
            <div className='profile_name'>
                <Typography className='name'>{data.name}</Typography>
                <Typography className='title'>{data.title}</Typography>
            </div>
            <figure className='profile_image'>
                <img src={selfimage} alt="" />
            </figure>
            <div className='profile_timeline'>
                <div className='timeline_profile'>
                <CustomTimeline className={'timeline'} icon={<TravelExploreIcon/>}>
                    <CustomChildTimeline title='Name' text={data.name}/>
                    <CustomChildTimeline title='Title' text={data.title}/>
                    <CustomChildTimeline title='Status' text={data.Job}/>
                    <CustomChildTimeline title='Email' text={data.Email}/>
                    <CustomChildTimeline title='Birthday' text={data.Birthday}/>
                </CustomTimeline>
                </div>
                <div className='custom_button3' onClick={() => openInNewTab('https://drive.google.com/file/d/1tdV9Kl-Ch4dSbbULnIciUG_ZxypOP17r/view?usp=sharing')}>
                    <Button  text={'Download Cv'} icon= {data.Buttons.Profile} >
                        Primary
                    </Button>
                </div>
                
            </div>
        </div>
    )
}

export default Profile
