import React from 'react';
import { MDBCard, MDBCardBody, MDBCardTitle, MDBCardText, MDBCardImage} from 'mdb-react-ui-kit';
import './Customcards.css';

function CustomCards({title,text, number , link}) {
    return (
        <MDBCard className='main_card' style={{ maxWidth: '15rem'}}>
        <MDBCardImage src={number ? 'https://mdbcdn.b-cdn.net/img/new/standard/nature/'+number+'.webp' : link} position='top' alt='Fissure in Sandstone'/>
        <MDBCardBody>
          <MDBCardTitle className='card_title'>{title}</MDBCardTitle>
          <MDBCardText className='card_text'>
            {text}
          </MDBCardText>
        </MDBCardBody>
      </MDBCard>
    );
}

export default CustomCards
